.nav {
  color: black;
  text-transform: uppercase;
  font-weight: bold;

  &__list {
    display: flex;
    justify-content: center;
  }

  &__item {
    padding: 10px 30px;
    margin: 0 22.5px;
    border-radius: 8px;
    font-size: 12px;
    letter-spacing: 2px;

    &_active {
      background-color: rgba($active-color, 0.1);
    }
  }

  &__link {
    color: white;
    text-decoration: none;
    transition: color $hover-duration;

    &--mobile {
      font-size: 22px;
      line-height: 22px;
    }

    &:hover {
      color: $active-color;
    }
  }
}

.nav__item > a:hover {
  color: #9fadc2;
  transition: 0.3s;
}

@media screen and (max-width: $small-screen-breakpoint) {
  .nav__item {
    display: none;
  }
}
